import tuiButton from "@/components/extend/button/button"
export default {
	components: {
		tuiButton
	},
	data() {
		return {}
	},
	methods: {
		go(page) {
			if (page == 1) {
				uni.switchTab({
					url: "/pages/index/index"
				})
			} else {
				uni.navigateTo({
					url: '../order/index'
				})
			}
		}
	}
}
